import React from 'react';
import axios from 'axios';
import { atom } from 'jotai'
import { loadable } from "jotai/utils"
import { atomWithImmer } from 'jotai/immer'
import { Login,Password} from "./types";



export function APIurl(APIURL:string){   //"/api/multicast-groups"
	return window.location.href.slice(0,window.location.href.indexOf("/",window.location.href.indexOf("//")+2)).concat(APIURL);
}

const BASEURL={baseURL:'https://reqres.in/api/'};

// https://stackoverflow.com/questions/40988238/sending-the-bearer-token-with-axios
// const config = {headers: { Authorization: `Bearer ${token}` }};
// Axios.post( 
//     const BASEURL,
//     bodyParameters,
//     config
//   ).then(console.log).catch(console.log);


export const Authenticate = atomWithImmer<Login>({authenticated:false,role:"",token:""});
export const PasswordUser = atomWithImmer<Password>({username:"",password:""});
const AsyncUser = atom(async (get) => {
    const pdum = get(PasswordUser);
    console.log("atom-pdun",pdum);
    const response = await axios.create(BASEURL).post('/login', {username:pdum.username,password:pdum.password})
    const data = await response.data;
    console.log("response",response);
    return data;
});
export const loadableAsyncUser = loadable(AsyncUser);
